 * @Description: 流程审批代理
 * @Autor: lwq
 * @Date: 2020-07-29 09:31:57
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2020-09-04 18:38:41
 * @FilePath: \mrc-base-server-view\src\views\approver\index.vue
--> 
<template>
  <div>
    <mrc_ess_table :headers='headers'
                   v-if="$route.name == 'approver'"
                   pageName='addApprover'
                   :paginationOptions='paginationOptions'
                   @turnPage='getList'
                   :isLoading='loading'
                   :slotKey="['effective_start','agent_staff_uuid']"
                   :items='desserts'>
      <template v-slot:item.agent_staff_uuid='{item}'>
        <mrc_ess_staffSpan :list='item'></mrc_ess_staffSpan>
      </template>
      <template v-slot:item.effective_start='{item}'>
        <h4>{{item.effective_start|changeDate}} - {{item.effective_end|changeDate}}</h4>
      </template>
      <template v-slot:item.actions='{item}'>
        <menu-list @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { approverList, delApprover } from '@/service/api/approver'
export default {
  components: {},
  props: {},
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  data () {
    return {
      loading: false,
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },

      desserts: []
    };
  },
  validations: {},
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_The_acting_employee'),
          align: 'start',
          sortable: false,
          value: 'agent_staff_uuid',
        },
        {
          text: this.$t('label_Date'),
          align: 'start',
          sortable: false,
          value: 'effective_start',
        },
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions'
        }
      ]
    },
    list () {
      return [
        {
          name: this.$t('label_Edit')
        },
        {
          name: this.$t('label_delete')
        }
      ]
    },
  },
  created () { },
  watch: {
    async '$route' (val) {
      if (val.name == 'approver') {
        await this.getList(this.paginationOptions)
      }
    }
  },
  async mounted () {
    await this.getList(this.paginationOptions)
  },

  methods: {
    async getList (obj) {
      if (obj) {
        this.paginationOptions = obj
      }
      this.loading = true
      const res = await approverList({
        cpage: this.paginationOptions.cpage,
        pagesize: this.paginationOptions.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.paginationOptions.totalCount = res.totalCount
        this.paginationOptions.totalpage = res.totalpage
      }
      this.loading = false
    },
    /**
     * @description: 进入详情
     * @param {index} 点的第几个menu 
     * @param {item} 点的整条数据
     * @return: 
     * @author: lwq
     */
    handleClick ({ item, index }, obj) {
      if (item.type == 'view') {
        this.$store.commit('set_subPage_name', obj.agent_staff_uuid)
        this.$store.commit('set_uuid', obj.uuid)

        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'detailApprover',
          params: {
            name: obj.agent_staff_uuid,
            uuid: obj.uuid
          }
        })
      }
      if (item.type == 'edit') {
        this.$store.commit('set_subPage_name', obj.agent_staff_uuid)
        this.$store.commit('set_uuid', obj.uuid)

        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'detailApprover',
          params: {
            name: obj.agent_staff_uuid,
            uuid: obj.uuid
          }
        })
      }
      if (item.type == 'del') {
        this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
          .then(async () => {
            const del = await delApprover({
              uuid: obj.uuid
            })
            if (del.code == 200) {
              this.$Message({ text: this.$t('message_The_deletion_was_successful') })
              // this.desserts.length === 1  只有一条数据 删除就请求上一页的数据
              if (this.desserts.length === 1 && this.paginationOptions.cpage > 1) {
                this.paginationOptions.cpage--
              }
              await this.getList()
            }
          }).catch(() => { })
      }
    },
  },
};
</script>

