import http from '../httpConfig'

const table = "mrbase_sys_flow_proxy_approval"

// 列表
export const approverList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/proxy_approval/list-approval/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
